import React, { Component } from 'react'
import Converter from './Converter'
export default class ContactUs extends Component {
  getMarkDownContent() {
    const content = `
#Contact Us

# GDD
GDD is a research group of [LS2N](http://www.google.com/url?q=http%3A%2F%2Fls2n.fr&sa=D&sntz=1&usg=AFQjCNHy6s6CwrzokZFpYPk24D1m6MWHjg), [University of Nantes](http://www.google.com/url?q=http%3A%2F%2Fwww.univ-nantes.fr%2F&sa=D&sntz=1&usg=AFQjCNHKMWsK8VIf-sCLC2CcKz4Jwai7Kw). GDD stands for "Gestion de Données Distribuées" that can be translated to "Distributed Data Management". GDD is working in federated distributed systems following 3 research directions:

-   **federated data structures and consistency,**
-   **collaborative data sharing in federations,**
-   **security and usage control in federations.**

GDD has expertise in [Distributed Systems](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FDistributed_system&sa=D&sntz=1&usg=AFQjCNFsEi7VbzZWwJFeJ9VVTIjeGi-K4w), [Distributed Collaborative Systems](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FCSCW&sa=D&sntz=1&usg=AFQjCNEfL7lo_wBiI2usTnRukK_yVPTizw), [Distributed Database systems](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FDistributed_database_management_system&sa=D&sntz=1&usg=AFQjCNEHmlLVpoNG3xR7KdUIrMI34UKBzg) and [Web of Data](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FSemantic_Web&sa=D&sntz=1&usg=AFQjCNGqR96_jR2bYPiqrzEH3iYOEm5vCQ). Our topics includes:

-   **Fundations of distributed system; [distributed algorithms](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FDistributed_algorithm&sa=D&sntz=1&usg=AFQjCNFC6VP-BmJSNckEukWxaCU3ZZ6pIA), [consistency](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FConsistency_model&sa=D&sntz=1&usg=AFQjCNGCe0Lh75Ls1CTYv5MGcpRF2qpvhg), [replication](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FReplication_%28computing%29&sa=D&sntz=1&usg=AFQjCNFf7-XOm9rgw7cXiF_Pus2e_HnLyA), [availability](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FCAP_theorem&sa=D&sntz=1&usg=AFQjCNG_POWHTdZqm4afB2Z9O1btlcZs3Q), [fault-tolerance](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FCAP_theorem&sa=D&sntz=1&usg=AFQjCNG_POWHTdZqm4afB2Z9O1btlcZs3Q), [scalability](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FScalability&sa=D&sntz=1&usg=AFQjCNHNoEuH67HOthpXHPxDoVA5OY9mGA),**
-   **Distributed Data Management in [Web of Data](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FSemantic_Web&sa=D&sntz=1&usg=AFQjCNGqR96_jR2bYPiqrzEH3iYOEm5vCQ): [data integration](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FData_integration&sa=D&sntz=1&usg=AFQjCNEH0VDplcrxOmPsz1ERJ2QKLdxFVA), [social semantic web](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FSocial_Semantic_Web&sa=D&sntz=1&usg=AFQjCNGc1OMp4I1hKmdRAsGHm-ns3FYQDA), linked data,**
-   **Privacy and confidentiality in distributed data management**

These topics are present in the following scientific domains:

-   [**Distributed and Parallel Computing**](http://www.google.com/url?q=http%3A%2F%2Facademic.research.microsoft.com%2FRankList%3Fentitytype%3D3%26topDomainID%3D2%26subDomainID%3D16%26last%3D0%26start%3D1%26end%3D100&sa=D&sntz=1&usg=AFQjCNEYCEQAszGp_F78W2Sq9PU321UmTw) **(ICDCS, PODC),**
-   **[Databases](http://www.google.com/url?q=http%3A%2F%2Facademic.research.microsoft.com%2FRankList%3Fentitytype%3D3%26topDomainID%3D2%26subDomainID%3D18%26last%3D0%26start%3D1%26end%3D100&sa=D&sntz=1&usg=AFQjCNFGu2aZo7Dn5_qGyOja7ilSfcud3g) (VLDB,SIGMOD),**
-   **[WWW](http://www.google.com/url?q=http%3A%2F%2Facademic.research.microsoft.com%2FRankList%3Fentitytype%3D3%26topDomainID%3D2%26subDomainID%3D15%26last%3D0%26start%3D1%26end%3D100&sa=D&sntz=1&usg=AFQjCNGiLFfYRWDZSit7aYmh-pASQQoZgA) and [Semantic web](http://www.google.com/url?q=http%3A%2F%2Fen.wikipedia.org%2Fwiki%2FSemantic_Web&sa=D&sntz=1&usg=AFQjCNGqR96_jR2bYPiqrzEH3iYOEm5vCQ) (WWW,ISWC).**

GDD is member of [Labex Comin Labs](http://www.google.com/url?q=http%3A%2F%2Fwww.cominlabs.ueb.eu%2F&sa=D&sntz=1&usg=AFQjCNFLSaeGUJN82jDo54bDjcxjyw4fZQ). The CominLabs federate the best teams from Bretagne and Nantes regions in the broad area of telecommunications, from electronic devices to wide area distributed applications “over the top”.


# GDD Team members
## Administrative Assistant

-   [Sandrine THENOT](https://www.google.com/url?q=https%3A%2F%2Fls2n.fr%2Fsandrine%2F&sa=D&sntz=1&usg=AFQjCNGuaeDfcUxvxZ-lOilEzKZHJGIvwQ), Administrative assistant.

## Professors

-   [**Pascal Molli**](http://www.google.com/url?q=http%3A%2F%2Fpagesperso.lina.univ-nantes.fr%2F~molli-p&sa=D&sntz=1&usg=AFQjCNF7LKzC0vYkmfFTa5fMwjcJgxhl9g), Full Professor at University of Nantes since September 2010, Head of GDD.
-   [**Achour Mostéfaoui**](https://scholar.google.fr/citations?user=q50kTWoAAAAJ&hl=fr), Full Professor at University of Nantes since November 2011, vice-head of GDD.

## Associated Professors

-   [**Matthieu Perrin**](https://scholar.google.fr/citations?user=WlNyiPkAAAAJ&hl=fr), Associate Professor at University of Nantes, since September 2017
-   [**Emmanuel Desmontils**](http://www.google.com/url?q=http%3A%2F%2Fwww.univ-nantes.fr%2Fdesmontils-e&sa=D&sntz=1&usg=AFQjCNGgkNtrHJvUpbZB7ycI_bMxgK7rFQ), Associate Professor at University of Nantes, since September 1998.
-   [**Patricia Serrano-Alvarado**](http://www.google.com/url?q=http%3A%2F%2Fpagesperso.lina.univ-nantes.fr%2F~serrano-p%2FPatricia_Serrano_Alvarado%2F&sa=D&sntz=1&usg=AFQjCNFG3G4k1wKUtD8VHQPad-Hj0jdYgg), Associate Professor at University of Nantes.
-   [**Hala Skaf-Molli**,](http://www.google.com/url?q=http%3A%2F%2Fpagesperso.lina.univ-nantes.fr%2F~skaf-h%2F&sa=D&sntz=1&usg=AFQjCNGAPA8fOx5Oz3iGMpWNLfZwdyR8bQ) Associate Professor at University of Nantes, since October 2010.
-   [**Gilles Nachouki**](http://www.google.com/url?q=http%3A%2F%2Fwww.lina.univ-nantes.fr%2F%3F_Gilles-NACHOUKI_.html&sa=D&sntz=1&usg=AFQjCNHQEUhwsr8EhGv-tBG1U2QqQYUXMQ), Associate Professor at University of Nantes.

## PostDoc

-   Sarah El Hassad, Started in March 2018
-   [Brice Nédelec](https://scholar.google.com/citations?user=LciNyd8AAAAJ&hl=fr&oi=ao), started in May 2017

## Engineer

-   [Nourredine Haouari](http://www.google.com/url?q=http%3A%2F%2Fwww.haouari.com%2F&sa=D&sntz=1&usg=AFQjCNH1naM4qFLV_hgihs6KeM4II2aKSA), Started in February 2018

## External Collaborator

-   [Alban Gaignard](http://www.google.com/url?q=http%3A%2F%2Fwww.univ-nantes.fr%2Fsite-de-l-universite-de-nantes%2Falban-gaignard--1166131.kjsp&sa=D&sntz=1&usg=AFQjCNH2fS0UIbn7OG-2hvzVkcmdottLkg), CNRS engineer, Institut du thorax, Inserm UMR 1087 / CNRS UMR 6291

## Ph.D. Students

-   [**Thomas Minier**](https://scholar.google.fr/citations?user=lKoIrIYAAAAJ&hl=fr)**,** Started in September 2017. Advisor: Pascal Molli, Co-Advisor: Hala Skaf
-   **Arnaud Grall,** Started in October 2017, Advirsr: Pascal Molli, Co-Advisor: Hala Skaf
-   **Grégoire Bonin,** Started in September 2017, Advisor: Achour Mostefaoui, Co-advisor: Matthieu Perrin
-   **Benjamin Moreau,** Started in October 2017, Advisor: Pascal Molli, Co-Advisors: Patricia Serrano & Emmanuel Desmontils

`

    return content
  }

  render() {
    return (
      <div
        className="textContent"
        dangerouslySetInnerHTML={{
          __html: Converter.getHtmlFromMarkDown(this.getMarkDownContent())
        }}
      />
    )
  }
}
