import React, { Component } from 'react'
import Converter from './Converter'
export default class About extends Component {
  getMarkDownContent() {
    const content = `
#About
## Web of Browser’s

The project "Web of Browsers" in collaboration with the [ASAP](https://www.irisa.fr/asap/) team of IRISA and the company [APIZEE](https://apizee.com/). It's about building a web capable of running without web servers, just using the capabilities of web browsers.

Web browsers are the most popular runtime environment in the world. Originally they was a simple HTML readers, but they can run complex applications that interact with humans and web services. The recent introduction of WebRTC has further expanded the capabilities of browsers with support of direct communications between browsers. Browsers become decentralized execution environments where interactions between humans and web services are possible without the use of third parties.


This is a major evolution for: 

- **Privacy protection**: Many services no longer depend on service providers to function;
- **For the right to be forgotten**: the services are fed by the navigators of the participants; data and services disappear if none of the participants wish to maintain them;
for scalability: Millions of participants together make an impressive computational force, crowd computing and storage.

In the "*Web of Browsers*" vision, the Web works without a server. It is massively decentralized and ephemeral. Web pages are hosted by web browser networks connected to each other through WebRTC. WeBrowse enables mass collaboration between participants without costly infrastructure while maintaining confidentiality of the exchanges. WeBrowse allows users to edit web pages anytime, anywhere, regardless of the number of participants.

In the "**Web of Browsers**" view, URIs no longer refer to HTML pages or data, but to **Web sessions**. A web session is a network of browsers hosting documents (HTML pages, data, etc.). As in the classic Web, a document can reference other documents using session URIs. Navigation is possible. If a user follows the link of a web session, the user's web browser joins the browser network of that session and hosts the documents for that session.
Just like in Google Docs, every web session lets you edit documents or data in real time. However, unlike Google Docs, Web sessions do not depend on a central server and do not have limits on the number of browsers that modify a particular document --- as demonstrated by the CRATE prototype [NMM16, NMM17] .

The Web browsers is quite different from the classic Web:
- This web forgets by default, i.e., if no user wants to share a web page, then this page disappears. We think it's a good thing. Indeed, the persistence of a page should require additional effort when the loss of information (forgetting) should be the default behavior.
- This Web can be more respectful of privacy: Web pages can work without the intervention of third parties.
- This Web can be hosted on high latency or disconnected networks. Sending information is based on the spread of rumors, i.e, this Web can be deployed on adhoc or very high latency networks.
This Web is able to evolve in real time.

The goal of this project is to build and experiment the **Web Browsers**. We are targeting events such as Open and Massive Online Courses (MOOCs), TV shows, conferences bringing together many people [BPD13]. We believe that WeBrowse should allow users to edit web pages anytime, anywhere, regardless of the number of participants. Even if only a small group of users among millions of participants wrote, all participants must be able to read and write in real time when they wish. In 2013, Coursera gathered 41,000 students for a MOOC entitled "Fundamentals of Online Education: Planning and Application". However, the course relied on Google's collaborative tools that allow simultaneous editing only to a limited number of users. The result was described as a "disaster" according to the journalists [ORE13, STR13]. This example clearly demonstrates the relevance of this use case for large groups. [TRA12] reports similar problems in a large-scale collaborative publishing context.

## References

- [NMM17] Brice Nédelec, Pascal Molli, Achour Mostefaoui. A scalable sequence encoding for collaborative editing. Concurrency and Computation: Practice and Experience. DOI:10.1002/cpe.4108

- [NMM16] Brice Nédelec, Pascal Molli, Achour Mostéfaoui, CRATE: Writing Stories Together with our Browsers. Demo 25th World Wide Web Conference 2016. CRATE is a real-time distributed and decentralized CollaboRATive Editor running in web browsers. [https://github.com/Chat-Wane/CRATE](https://github.com/Chat-Wane/CRATE)

- [BPD13] L. B. Breslow, D. E. Pritchard, J. DeBoer, G. S. Stump, A. D. Ho, and D. T. Seaton. Studying learning in the worldwide classroom: Research into edx’s first mooc. Research & Practice in Assessment, 8:13–25, 2013.

- [ORE13] W. Oremus. Online class on how to teach online classes goes laughably awry. Slate US, february 2013.

- [STR13] V. Strauss. How online class about online learning failed miserably. Washington Post, february 2013.

- [TRA12] B. Tomlinson, J. Ross, P. Andre, E. Baumer, D. Patterson, J. Corneli, M. Mahaux, S. Nobarany, M. Lazzari, B. Penzenstadler, A. Torrance, D. Callele, G. Olson, S. Silberman, M. Stunder, F. R. Palamedi, A. A. Salah, E. Morrill, X. Franch, F. F. Mueller, J. J. Kaye, R. W. Black, M. L. Cohn, P. C. Shih, J. Brewer, N. Goyal, P. Nakki, J. Huang, N. Baghaei, and C. Saper. Massively distributed authorship of academic papers. In CHI ’12 Extended Abstracts on Human Factors in Computing Systems, CHI EA ’12, pages 11–20, New York, NY, USA, 2012. ACM.

- [FMP+16] Davide Frey, Achour Mostefaoui, Matthieu Perrin, Pierre-Louis Roman, and François Taïani. Speed for the elite, consistency for the masses: differentiating eventual consistency in large-scale distributed systems, Proceedings of the 35th Symposium on Reliable Distributed Systems (SRDS 2016), Budapest, Hungary, September, 2016 (10p.)

- [NTD15] Brice Nédelec, Julian Tanke, Davide Frey, Pascal Molli, Achour Mostefaoui. Spray: an Adaptive Random Peer Sampling Protocol. [Technical Report] LINA-University of Nantes; INRIA Rennes - Bretagne Atlantique. 2015. <hal-01203363>
`
    return content
  }

  render() {
    return (
      <div
        dangerouslySetInnerHTML={{
          __html: Converter.getHtmlFromMarkDown(this.getMarkDownContent())
        }}
        className="textContent"
      />
    )
  }
}
